import { setup } from "js/app";
import "sasspages/skills";
import { preloaderIndeterminate } from "js/common";

const $ = document.querySelector.bind(document);
const $$ = document.querySelectorAll.bind(document);
const displayType = {
    Tree: 'tree',
    Bubble: 'bubble',
    List: 'list'
};

setup(); // sets up core site functionality 
console.log("i am skills.js");

let chart = null,
    series = null,
    isInitialData = true,
    isBubbleChart = true,
    divSkillControls = null,
    dtype = displayType.List,
    isFiltered = false;

document.addEventListener("DOMContentLoaded", async () => {

    //#region Controls

    const txtSearch = $("#txtSearch");
    // const divSearch = $("div.search");
    const svgClearSearch = $("#clearSearch");
    divSkillControls = $("#skill-controls");
    const rbTree = $("#rbTree"),
        rbList = $("#rbList"),
        rbBubble = $("#rbBubble");
    //#endregion

    showLoading();

    //#region control event handlers
    document.addEventListener("sidebar-changed", (info) => {
        import(/* webpackChunkName: "amcharts4-core" */"@amcharts/amcharts4/core").then((am4core) => {
            am4core.system.isPaused = true;
            console.log("am4core.system.isPaused");
            setTimeout(() => { am4core.system.isPaused = false; }, 600);
        });
    });



    //#region RadioButton-Handlers

    const $rbs = $(".view-selection").querySelectorAll(":scope input");

    // $rbs.forEach(el => el.addEventListener("click", function (e) {
    //     $rbs.forEach(r => r.removeAttribute("disabled"));
    //     this.setAttribute("disabled", "disabled");      
    // }));

    function disableOnlyMe() {
        $rbs.forEach(r => r.removeAttribute("disabled"));
        this.setAttribute("disabled", "disabled");
    }


    rbTree.addEventListener("click", async function (e) {

        disableOnlyMe.call(this);
        chart && chart.dispose();
        dtype = displayType.Tree;
        let { data } = getData();
        if (data.length > 0) {
            ({ chart } = await CreateTreeMapChart());
            chart.data = data;
        }
    });

    rbBubble.addEventListener("click", async function (e) {

        disableOnlyMe.call(this);
        chart && chart.dispose();
        dtype = displayType.Bubble;
        let { data } = getData();
        if (data.length > 0) {
            ({ series, chart } = await CreateBubbleChart());
            chart.data = data;
        }
    });

    rbList.addEventListener("click", function (e) {

        disableOnlyMe.call(this);
        chart && chart.dispose();
        dtype = displayType.List;
        let { data } = getData();
        if (data.length > 0) {
            CreateListView(data);
        }
    });

    //#endregion

    const clearSearch = () => {
        if (!txtSearch.value.trim() && !isFiltered) return;
        txtSearch.value = "";
        displaySkills();
    };

    svgClearSearch.addEventListener("click", clearSearch);

    const searchHandler = function (e) {
debugger;
        try {
            txtSearch.removeEventListener("keydown", searchHandler);

            if (!isFiltered && this.value.trim() === "") return;

            if (!e) e = window.event;
            var keyCode = e.keyCode || e.which;
            if (keyCode == '13') { // Enter pressed                     
                displaySkills();
            }
            if (keyCode == '27') { // ESC pressed                  
                clearSearch();
            }

        } catch (error) {
            console.log(error);
        } finally {
            txtSearch.addEventListener("keydown", searchHandler);
        }
    };

    txtSearch.addEventListener("keydown", searchHandler);
    //#endregion
    let { data } = getData();
    CreateListView(data);

});

function showLoading() {
    divSkillControls.innerHTML = preloaderIndeterminate;
}


async function displaySkills() {

    let { data, filter } = getData();
    switch (dtype) {
        case displayType.List:
            CreateListView(data);
            break;
        case displayType.Bubble:
            ({ chart, series } = await CreateBubbleChart());
            debugger;
            filter && (series.maxLevels = 2);
            chart.data = data;
            break;
        case displayType.Tree:
            ({ chart } = await CreateTreeMapChart());
            // filter && (chart.maxLevels = 2);
            debugger;
            chart.data = data;
            break;
        default:
            break;
    }
}

//#region  SEARCH
function search(text) {
    return getRootData().reduce((acc, c, idx) => {
        let arrItems = c.children.filter((item, i, arr) => { return item.name.toLowerCase().indexOf(text) !== -1; });

        if (arrItems.length > 0) {
            let cat = {
                name: c.name,
                collapsed: true,
                children: arrItems
            };

            acc.push(cat);
        }
        return acc;
    }, []);
}


const getData = () => {
    let term = txtSearch.value.trim();
    isFiltered = !!term;
    let data = isFiltered ? search(term.toLowerCase()) : getRootData();
    return { data: data, filter: isFiltered };
};

//#endregion

//#region CONTROLS SETUP
async function CreateTreeMapChart() {

    createChartDiv();

    let promiseCore = import(/* webpackChunkName: "amcharts4-core" */"@amcharts/amcharts4/core");
    let promiseCharts = import("@amcharts/amcharts4/charts");
    let promiseAnimated = import(/* webpackChunkName: "amcharts4-animated" */"@amcharts/amcharts4/themes/animated");
    let promiseForeceDirected = import(/* webpackChunkName: "amcharts4-forceDirected" */"@amcharts/amcharts4/plugins/forceDirected");

    return await Promise.all([promiseCore, promiseAnimated, promiseForeceDirected, promiseCharts]).then((modules) => {
        let am4core = modules[0];
        let am4themes_animated = modules[1].default;
        let am4plugins_forceDirected = modules[2];
        let am4charts = modules[3];


        am4core.useTheme(am4themes_animated);

        chart = am4core.create("chartdiv", am4charts.TreeMap);
        chart.hiddenState.properties.opacity = 0; // this makes initial fade in effect
        // only one level visible initially
        chart.maxLevels = 1;

        chart.showBalloon = false;
        // define data fields
        chart.dataFields.value = "value";
        chart.dataFields.name = "name";
        chart.dataFields.children = "children";
        chart.homeText = "Technical Skills Overview";
        // enable navigation
        chart.navigationBar = new am4charts.NavigationBar();
        // level 0 series template
        var level0SeriesTemplate = chart.seriesTemplates.create("0");
        level0SeriesTemplate.strokeWidth = 2;
        // by default only current level series bullets are visible, but as we need brand bullets to be visible all the time, we modify it's hidden state
        level0SeriesTemplate.bulletsContainer.hiddenState.properties.opacity = 1;
        level0SeriesTemplate.bulletsContainer.hiddenState.properties.visible = true;
        //
        var level0_column = level0SeriesTemplate.columns.template;
        level0_column.tooltipText = "{name}";

        // create hover state
        var columnTemplate = level0SeriesTemplate.columns.template;
        var hoverState = columnTemplate.states.create("hover");

        // darken
        hoverState.adapter.add("fill", function (fill, target) {
            if (fill instanceof am4core.Color) {
                return am4core.color(am4core.colors.brighten(fill.rgb, -0.2));
            }
            return fill;
        });
        // add logo
        var image = columnTemplate.createChild(am4core.Image);
        image.opacity = 0.15;
        image.align = "center";
        image.valign = "middle";
        image.width = am4core.percent(80);
        image.height = am4core.percent(80);

        // add adapter for href to load correct image
        image.adapter.add("href", function (href, target) {
            var dataItem = target.parent.dataItem;
            if (dataItem) {
                // console.log(window.location.origin + "/" + dataItem.treeMapDataItem.name.toLowerCase() + ".png");
                // console.log(require.resolve(`../../img/skills/${ dataItem.treeMapDataItem.name.toLowerCase()}`))
                // return "http://legends.io/" + dataItem.treeMapDataItem.name.toLowerCase() + ".png";

                // return window.location.origin + "/" + dataItem.treeMapDataItem.name.toLowerCase() + ".png";
                return window.location.origin + "/" + require(`images/skills/${dataItem.treeMapDataItem.name}.png`).default;
            }
        });
        // level1 series template
        var level1SeriesTemplate = chart.seriesTemplates.create("1");
        level1SeriesTemplate.columns.template.fillOpacity = 0;

        var level1_column = level1SeriesTemplate.columns.template;
        level1_column.tooltipText = "{name}";

        var bullet1 = level1SeriesTemplate.bullets.push(new am4charts.LabelBullet());
        bullet1.locationX = 0.5;
        bullet1.locationY = 0.5;
        bullet1.label.text = "{name}";
        bullet1.label.fill = am4core.color("#ffffff");
        // level2 series template
        var level2SeriesTemplate = chart.seriesTemplates.create("2");
        level2SeriesTemplate.columns.template.fillOpacity = 0;
        var bullet2 = level2SeriesTemplate.bullets.push(new am4charts.LabelBullet());
        bullet2.locationX = 0.5;
        bullet2.locationY = 0.5;
        bullet2.label.text = "{name}";
        bullet2.label.fill = am4core.color("#ffffff");
        return Promise.resolve({ chart });
    });
}

async function CreateBubbleChart() {
    createChartDiv(); // <div id="chartdiv"></div>   

    // import * as am4core from "@amcharts/amcharts4/core";
    // import * as am4charts from "@amcharts/amcharts4/charts";
    // import am4themes_animated from "@amcharts/amcharts4/themes/animated";
    // import * as am4plugins_forceDirected from "@amcharts/amcharts4/plugins/forceDirected";

    let promiseCore = import(/* webpackChunkName: "amcharts4-core" */"@amcharts/amcharts4/core");
    // let promiseCharts = import("@amcharts/amcharts4/charts");
    let promiseAnimated = import(/* webpackChunkName: "amcharts4-animated" */"@amcharts/amcharts4/themes/animated");
    let promiseForeceDirected = import(/* webpackChunkName: "amcharts4-forceDirected" */"@amcharts/amcharts4/plugins/forceDirected");

    return await Promise.all([promiseCore, promiseAnimated, promiseForeceDirected]).then((modules) => {
        let am4core = modules[0];
        let am4themes_animated = modules[1].default;
        let am4plugins_forceDirected = modules[2];

        // am4core.useTheme(am4themes_animated);

        chart = am4core.create("chartdiv", am4plugins_forceDirected.ForceDirectedTree);
        // chart.preloader.hiddenState.transitionDuration = 0;
        // chart.preloader.progress = 0.5.
        series = chart.series.push(new am4plugins_forceDirected.ForceDirectedSeries());
        // series.data = data;
        series.maxLevels = 2;
        // networkSeries.dataFields.collapsed = true;
        series.dataFields.linkWith = "linkWith";
        series.dataFields.name = "name";
        series.dataFields.id = "name";
        series.dataFields.value = "value";
        series.dataFields.children = "children";
        series.links.template.distance = 1;

        series.nodes.template.adapter.add("tooltipText", function (text, target) {
            return target.dataItem.name;
        });

        series.nodes.template.fillOpacity = 1;
        series.nodes.template.outerCircle.scale = 1;
        series.nodes.template.label.text = "{name}";
        series.fontSize = 12;
        series.nodes.template.label.hideOversized = true;
        series.nodes.template.label.truncate = true;
        series.minRadius = am4core.percent(2);
        series.manyBodyStrength = -5;
        series.links.template.strokeOpacity = 0;

        return Promise.resolve({ chart, series });

    }).catch((error) => {
        console.log(error);
    });
}


function CreateListView(data) {

    clearControls();
    createListViewDiv();


    //#region listview template
    let strTmpl = `<div class="list-group">
                        <div class="header">
                            <div class="switch">   
                            <label>
                            Collapse all
                                <input id="cbExpand" type="checkbox">
                                <span class="lever"></span>
                                Expand all
                            </label>
                            </div>
                        </div>
                        <% for(var i = 0; i < cats.length; i++) { %>
                        <a href="#" class="list-group-header">
                            <h6 class="list-group-item-heading"><%= cats[i].name %></h6>
                        </a>
                        <div class="list-group-body section">
                            <ul>
                            <% let len = cats[i].children.length;  %>
                            <% for(var c = 0; c < len; c++) { %>
                                <li>
                                    <%= cats[i].children[c].name %>
                                </li>
                           
                            <% } %>
                            </ul>
                        </div>
                        <% } %>
                    </div>`;
    //#endregion

    let lib2 = import(/* webpackChunkName: "lib2" */ "js/lib2");

    import(/* webpackChunkName: "lodash" */ 'lodash-es/template').then(({ default: template }) => {

        lib2.then((lib) => {

            let compiledTmpl = template(strTmpl);
            let result = compiledTmpl({ cats: data });
            $("#listView").innerHTML = result;
            let container = $(".list-group");
            lib.jm.collapsible.init(container, true);

            isFiltered && container.querySelectorAll(":scope > .list-group-header").forEach(el => el.classList.add("open"));

            $("#cbExpand").addEventListener("click", function (e) {
                e.stopPropagation();
                if (this.checked) {
                    container.querySelectorAll(":scope > .list-group-header:not(.open)").forEach((el) => {
                        // el.classList.add("open");
                        el.click();
                    });
                } else {
                    container.querySelectorAll(":scope > .list-group-header.open").forEach((el) => {
                        // el.classList.remove("open");
                        el.click();
                    });
                }
            });
        });

    }).catch(error => console.log('An error occurred while loading the component'));

}



//#endregion

// #region HELPERS

function createListViewDiv() {
    clearControls();
    let listView = document.createElement("div");
    listView.id = "listView";
    divSkillControls.appendChild(listView);
}


function createChartDiv() {
    chart && chart.dispose();
    clearControls();
    let chartdiv = document.createElement("div");
    chartdiv.id = "chartdiv";
    divSkillControls.appendChild(chartdiv);
}

function clearControls() {
    divSkillControls.innerHTML = "";
}

